export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "en": {
        "hello": (()=>{const fn=(ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Hello i18n in SFC!"])};fn.source="Hello i18n in SFC!";return fn;})()
      }
    }
  })
}
